import React, { useState, useMemo } from 'react';
import { 
  Sparkles, ArrowRight, X, Loader2, CheckCircle,
  Building2, TreePine, Factory, Clock, Palette,
  CircleDot, Leaf, Zap, Circle,
  DollarSign, Gem, Crown, Target,
  Sofa, Paintbrush, Shield, RefreshCw, TreeDeciduous,
  Tv, BedDouble, UtensilsCrossed, Briefcase, Home,
  ShoppingBag, Star
} from 'lucide-react';
import { generateText } from '../../services/geminiService';
import { saveStyleDNA, getPersonalizedRecommendations } from '../../services/userPreferencesService';

/**
 * AIStyleQuiz - Szemlyre szabott stlus kvz Gemini AI-val
 * Teljes adatbzis ismeretvel
 */
const AIStyleQuiz = ({ products, onRecommendations, onClose }) => {
  const [step, setStep] = useState(0);
  const [answers, setAnswers] = useState({});
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [styleDNA, setStyleDNA] = useState(null);
  const [recommendations, setRecommendations] = useState([]);
  const [aiExplanation, setAiExplanation] = useState('');

  // Termk statisztikk az AI szmra
  const productStats = useMemo(() => {
    if (!products || products.length === 0) return null;
    
    const categories = {};
    const priceRanges = { budget: 0, mid: 0, premium: 0 };
    
    products.forEach(p => {
      // Kategrik
      const cat = (p.category || 'Egyb').split(' > ')[0];
      categories[cat] = (categories[cat] || 0) + 1;
      
      // rkategrik
      const price = p.salePrice || p.price || 0;
      if (price < 100000) priceRanges.budget++;
      else if (price <= 300000) priceRanges.mid++;
      else priceRanges.premium++;
    });
    
    const topCategories = Object.entries(categories)
      .sort((a, b) => b[1] - a[1])
      .slice(0, 10)
      .map(([name, count]) => `${name} (${count})`)
      .join(', ');
    
    return { 
      total: products.length, 
      topCategories,
      priceRanges
    };
  }, [products]);

  const questions = [
    {
      id: 'space',
      question: 'Milyen a laksod jelenlegi stlusa?',
      icon: Home,
      options: [
        { id: 'modern', label: 'Modern & Minimalista', icon: Building2, color: 'text-blue-600 bg-blue-50', keywords: ['modern', 'minimalista', 'letisztult', 'egyszer', 'fehr'] },
        { id: 'scandinavian', label: 'Skandinv & Vilgos', icon: TreePine, color: 'text-emerald-600 bg-emerald-50', keywords: ['skandinv', 'natr', 'vilgos', 'fa', 'termszetes'] },
        { id: 'industrial', label: 'Indusztrilis & Nyers', icon: Factory, color: 'text-gray-600 bg-gray-100', keywords: ['indusztrilis', 'loft', 'fm', 'ipari', 'beton'] },
        { id: 'vintage', label: 'Vintage & Retro', icon: Clock, color: 'text-amber-600 bg-amber-50', keywords: ['vintage', 'retro', 'antik', 'klasszikus', 'rgi'] },
        { id: 'bohemian', label: 'Bohm & Sznes', icon: Palette, color: 'text-pink-600 bg-pink-50', keywords: ['bohm', 'sznes', 'egyedi', 'mints', 'kreatv'] },
      ]
    },
    {
      id: 'colors',
      question: 'Milyen szneket preferlsz?',
      icon: Palette,
      options: [
        { id: 'neutral', label: 'Semleges (Fehr, Bzs, Szrke)', icon: CircleDot, color: 'text-gray-500 bg-gray-50', keywords: ['fehr', 'bzs', 'szrke', 'krm', 'natr'] },
        { id: 'earth', label: 'Fldsznek (Barna, Zld)', icon: Leaf, color: 'text-amber-700 bg-amber-50', keywords: ['barna', 'zld', 'fa', 'di', 'tlgy'] },
        { id: 'bold', label: 'Mersz (Kk, Piros)', icon: Zap, color: 'text-red-600 bg-red-50', keywords: ['kk', 'piros', 'srga', 'lnk', 'sznes'] },
        { id: 'dark', label: 'Stt (Fekete, Antracit)', icon: Circle, color: 'text-gray-800 bg-gray-200', keywords: ['fekete', 'antracit', 'stt', 'grafit', 'jfekete'] },
        { id: 'pastel', label: 'Pasztell (Rzsaszn, Menta)', icon: Sparkles, color: 'text-secondary-500 bg-secondary-50', keywords: ['rzsaszn', 'menta', 'pasztell', 'halvny', 'lgy'] },
      ]
    },
    {
      id: 'budget',
      question: 'Mi a preferlt rkategrid?',
      icon: DollarSign,
      options: [
        { id: 'budget', label: 'Megfizethet (< 100k Ft)', icon: DollarSign, color: 'text-green-600 bg-green-50' },
        { id: 'mid', label: 'Kzp (100-300k Ft)', icon: Gem, color: 'text-blue-600 bg-blue-50' },
        { id: 'premium', label: 'Prmium (> 300k Ft)', icon: Crown, color: 'text-amber-600 bg-amber-50' },
        { id: 'flexible', label: 'Rugalmas', icon: Target, color: 'text-primary-500 bg-primary-50' },
      ]
    },
    {
      id: 'priority',
      question: 'Mi a legfontosabb szmodra?',
      icon: Target,
      options: [
        { id: 'comfort', label: 'Knyelem', icon: Sofa, color: 'text-primary-500 bg-primary-50', keywords: ['knyelmes', 'puha', 'tgas'] },
        { id: 'design', label: 'Design', icon: Paintbrush, color: 'text-pink-600 bg-pink-50', keywords: ['design', 'stlusos', 'elegns', 'divatos'] },
        { id: 'durability', label: 'Tartssg', icon: Shield, color: 'text-gray-600 bg-gray-100', keywords: ['tarts', 'strapabr', 'ers', 'masszv'] },
        { id: 'versatility', label: 'Sokoldalsg', icon: RefreshCw, color: 'text-blue-600 bg-blue-50', keywords: ['praktikus', 'talakthat', 'multifunkcis'] },
        { id: 'eco', label: 'Fenntarthatsg', icon: TreeDeciduous, color: 'text-green-600 bg-green-50', keywords: ['ko', 'natr', 'fenntarthat', 'bio'] },
      ]
    },
    {
      id: 'room',
      question: 'Melyik helyisgbe keresel btort?',
      icon: Home,
      options: [
        { id: 'living', label: 'Nappali', icon: Tv, color: 'text-primary-500 bg-primary-50', keywords: ['kanap', 'fotel', 'dohnyz', 'tv', 'nappali', 'lgarnitra', 'sarok'] },
        { id: 'bedroom', label: 'Hlszoba', icon: BedDouble, color: 'text-secondary-700 bg-secondary-50', keywords: ['gy', 'matrac', 'jjeli', 'hlszoba', 'gardrb', 'komd', 'franciagy'] },
        { id: 'dining', label: 'tkez', icon: UtensilsCrossed, color: 'text-amber-600 bg-amber-50', keywords: ['tkez', 'asztal', 'szk', 'tlal', 'tkezasztal', 'tkezszk'] },
        { id: 'office', label: 'Dolgozszoba', icon: Briefcase, color: 'text-gray-600 bg-gray-100', keywords: ['rasztal', 'iroda', 'forgszk', 'polc', 'knyvespolc'] },
        { id: 'all', label: 'Teljes laks', icon: Home, color: 'text-emerald-600 bg-emerald-50', keywords: [] },
      ]
    }
  ];

  const currentQuestion = questions[step];
  const progress = ((step + 1) / questions.length) * 100;

  const handleAnswer = (optionId) => {
    const newAnswers = { ...answers, [currentQuestion.id]: optionId };
    setAnswers(newAnswers);
    
    if (step < questions.length - 1) {
      setTimeout(() => setStep(step + 1), 200);
    } else {
      setTimeout(() => analyzeStyle(newAnswers), 300);
    }
  };

  const analyzeStyle = async (allAnswers) => {
    setIsAnalyzing(true);

    try {
      // sszegyjtjk a kulcsszavakat a vlaszok alapjn
      const selectedKeywords = [];
      questions.forEach(q => {
        const answer = allAnswers[q.id];
        const option = q.options.find(o => o.id === answer);
        if (option?.keywords) {
          selectedKeywords.push(...option.keywords);
        }
      });

      // AI-val keresnk termkeket
      const matchedProducts = await findMatchingProductsWithAI(allAnswers, selectedKeywords);
      setRecommendations(matchedProducts);

      // Stlus lersok
      const styleLabels = {
        modern: 'modern, minimalista',
        scandinavian: 'skandinv, vilgos',
        industrial: 'indusztrilis, loft',
        vintage: 'vintage, retro',
        bohemian: 'bohm, sznes'
      };
      
      const colorLabels = {
        neutral: 'semleges szneket (fehr, bzs, szrke)',
        earth: 'fldszneket (barna, zld)',
        bold: 'lnk szneket (kk, piros)',
        dark: 'stt tnusokat (fekete, antracit)',
        pastel: 'pasztell rnyalatokat'
      };

      const roomLabels = {
        living: 'nappaliba',
        bedroom: 'hlszobba',
        dining: 'tkezbe',
        office: 'dolgozszobba',
        all: 'a teljes laksba'
      };

      const budgetLabels = {
        budget: '100 ezer Ft alatt',
        mid: '100-300 ezer Ft kztt',
        premium: '300 ezer Ft felett',
        flexible: 'rugalmas kltsgvetssel'
      };

      // AI prompt a szemlyre szabott profilhoz
      const prompt = `Te egy profi lakberendez tancsad vagy. Kszts szemlyre szabott profilt s adj konkrt termkajnlsokat!

VSRL PREFERENCII:
- Stlus: ${styleLabels[allAnswers.space] || allAnswers.space}
- Sznek: ${colorLabels[allAnswers.colors] || allAnswers.colors}
- Helyisg: ${roomLabels[allAnswers.room] || allAnswers.room}
- Kltsgvets: ${budgetLabels[allAnswers.budget] || allAnswers.budget}

ELRHET TERMKEK (${productStats?.total || 0} db sszesen):
${matchedProducts.slice(0, 8).map(p => `- ${p.name}: ${(p.salePrice || p.price || 0).toLocaleString('hu-HU')} Ft`).join('\n')}

FELADATOD:
1. rj egy 2-3 mondatos szemlyre szabott stlusprofilt
2. Magyarzd el, MIRT ezeket a termkeket ajnlod (1 mondat)

Legyl bartsgos, hasznlj tegezst. Magyarul vlaszolj!`;

      const result = await generateText(prompt, { temperature: 0.8, maxTokens: 250 });

      let finalStyleDNA;
      if (result.success && result.text) {
        finalStyleDNA = result.text;
      } else {
        finalStyleDNA = getLocalStyleDNA(allAnswers);
      }
      
      setStyleDNA(finalStyleDNA);
      saveStyleDNA(allAnswers, finalStyleDNA);

      if (onRecommendations && matchedProducts.length > 0) {
        onRecommendations(matchedProducts);
      }

    } catch (error) {
      console.error('Quiz hiba:', error);
      const fallbackProducts = findMatchingProductsLocal(allAnswers);
      setRecommendations(fallbackProducts);
      const fallbackDNA = getLocalStyleDNA(allAnswers);
      setStyleDNA(fallbackDNA);
      saveStyleDNA(allAnswers, fallbackDNA);
    } finally {
      setIsAnalyzing(false);
    }
  };

  // AI-val tmogatott termk keress
  const findMatchingProductsWithAI = async (allAnswers, keywords) => {
    if (!products || products.length === 0) return [];
    
    // Elszr loklisan szrnk
    let filtered = [...products];
    
    // Budget szrs
    if (allAnswers.budget === 'budget') {
      filtered = filtered.filter(p => (p.salePrice || p.price) < 100000);
    } else if (allAnswers.budget === 'mid') {
      filtered = filtered.filter(p => {
        const price = p.salePrice || p.price;
        return price >= 100000 && price <= 300000;
      });
    } else if (allAnswers.budget === 'premium') {
      filtered = filtered.filter(p => (p.salePrice || p.price) > 300000);
    }

    // Helyisg s kulcssz alap szrs
    const roomOption = questions.find(q => q.id === 'room')?.options.find(o => o.id === allAnswers.room);
    const roomKeywords = roomOption?.keywords || [];
    const allKeywords = [...keywords, ...roomKeywords];

    // Pontozs a termkeknek
    const scored = filtered.map(p => {
      const productText = `${p.name || ''} ${p.category || ''} ${p.description || ''}`.toLowerCase();
      let score = 0;
      
      allKeywords.forEach(kw => {
        if (productText.includes(kw.toLowerCase())) {
          score += 10;
        }
      });
      
      // Kategria egyezs extra pont
      if (roomKeywords.length > 0) {
        roomKeywords.forEach(kw => {
          if (productText.includes(kw.toLowerCase())) {
            score += 5;
          }
        });
      }
      
      return { product: p, score };
    });

    // Top termkek
    const topProducts = scored
      .filter(s => s.score > 0)
      .sort((a, b) => b.score - a.score)
      .slice(0, 20)
      .map(s => s.product);

    // Ha van elg tallat, random vlogats a vltozatossgrt
    if (topProducts.length > 12) {
      return topProducts.sort(() => Math.random() - 0.5).slice(0, 12);
    }

    // Ha kevs a pontos tallat, tltsk fel relevns termkekkel
    if (topProducts.length < 6) {
      const additionalProducts = filtered
        .filter(p => !topProducts.some(tp => tp.id === p.id))
        .sort(() => Math.random() - 0.5)
        .slice(0, 12 - topProducts.length);
      
      return [...topProducts, ...additionalProducts];
    }

    return topProducts;
  };

  // Loklis fallback keress
  const findMatchingProductsLocal = (allAnswers) => {
    if (!products || products.length === 0) return [];
    
    let filtered = [...products];
    
    if (allAnswers.budget === 'budget') {
      filtered = filtered.filter(p => (p.salePrice || p.price) < 100000);
    } else if (allAnswers.budget === 'mid') {
      filtered = filtered.filter(p => {
        const price = p.salePrice || p.price;
        return price >= 100000 && price <= 300000;
      });
    } else if (allAnswers.budget === 'premium') {
      filtered = filtered.filter(p => (p.salePrice || p.price) > 300000);
    }

    const roomKeywords = {
      living: ['kanap', 'fotel', 'dohnyz', 'tv', 'nappali', 'lgarnitra'],
      bedroom: ['gy', 'matrac', 'jjeli', 'hlszoba', 'gardrb', 'komd'],
      dining: ['tkez', 'asztal', 'szk', 'tlal'],
      office: ['rasztal', 'iroda', 'forgszk', 'polc']
    };

    if (allAnswers.room && allAnswers.room !== 'all') {
      const keywords = roomKeywords[allAnswers.room] || [];
      if (keywords.length > 0) {
        const roomFiltered = filtered.filter(p => 
          keywords.some(kw => 
            (p.name || '').toLowerCase().includes(kw) || 
            (p.category || '').toLowerCase().includes(kw)
          )
        );
        if (roomFiltered.length > 0) {
          filtered = roomFiltered;
        }
      }
    }

    return filtered.sort(() => Math.random() - 0.5).slice(0, 12);
  };

  const getLocalStyleDNA = (allAnswers) => {
    const styleMap = {
      modern: 'Modern, letisztult zlsed van',
      scandinavian: 'A skandinv egyszersget kedveled',
      industrial: 'Az ipari, loft stlus hve vagy',
      vintage: 'A vintage bj rajongja vagy',
      bohemian: 'Egyedi, bohm stlusod van'
    };

    const priorityMap = {
      comfort: 'a knyelmet',
      design: 'az eszttikt',
      durability: 'a minsget',
      versatility: 'a praktikumot',
      eco: 'a krnyezettudatossgot'
    };

    return `${styleMap[allAnswers.space] || 'Egyedi zlsed van'}, s ${priorityMap[allAnswers.priority] || 'a minsget'} helyezed eltrbe. Az ajnlott termkek tkletesen illenek a stlusodhoz!`;
  };

  const formatPrice = (price) => (price || 0).toLocaleString('hu-HU') + ' Ft';

  const QuestionIcon = currentQuestion?.icon || Home;

  return (
    <div 
      className="fixed inset-0 z-[70] flex items-center justify-center bg-black/60 backdrop-blur-sm p-4"
      onClick={onClose}
    >
      <div
        className="bg-white rounded-2xl w-full max-w-lg max-h-[90vh] overflow-y-auto shadow-2xl"
        onClick={e => e.stopPropagation()}
      >
        {/* Header */}
        <div className="bg-gradient-to-r from-primary-500 to-secondary-700 p-5 relative">
          <button
            onClick={onClose}
            className="absolute top-3 right-3 w-8 h-8 flex items-center justify-center text-white/80 hover:text-white hover:bg-white/10 rounded-full transition-colors"
          >
            <X className="w-5 h-5" />
          </button>
          
          <div className="flex items-center gap-3 mb-3">
            <div className="w-12 h-12 bg-white/20 rounded-xl flex items-center justify-center">
              <Sparkles className="w-6 h-6 text-white" />
            </div>
            <div>
              <h2 className="text-xl font-bold text-white">Style DNA Quiz</h2>
              <p className="text-white/80 text-sm">
                {productStats ? `${productStats.total.toLocaleString()} termkbl vlogatunk` : 'Fedezd fel a stlusodat!'}
              </p>
            </div>
          </div>
          
          {/* Progress */}
          {!styleDNA && !isAnalyzing && (
            <div className="mt-4">
              <div className="flex justify-between text-xs text-white/80 mb-1.5">
                <span>Krds {step + 1}/{questions.length}</span>
                <span>{Math.round(progress)}%</span>
              </div>
              <div className="h-2 bg-white/20 rounded-full overflow-hidden">
                <div 
                  className="h-full bg-white rounded-full transition-all duration-300"
                  style={{ width: `${progress}%` }}
                />
              </div>
            </div>
          )}
        </div>

        {/* Content */}
        <div className="p-5">
          {isAnalyzing ? (
            <div className="py-12 text-center">
              <Loader2 className="w-12 h-12 text-primary-500 animate-spin mx-auto mb-4" />
              <h3 className="text-lg font-bold text-gray-900 mb-2">AI elemzs folyamatban...</h3>
              <p className="text-gray-500 text-sm">
                {productStats?.total.toLocaleString()} termkbl keressk a neked valkat
              </p>
            </div>
          ) : styleDNA ? (
            <div className="space-y-5">
              {/* Result */}
              <div className="bg-gradient-to-br from-primary-50 to-secondary-50 rounded-xl p-5 border border-primary-100">
                <div className="flex items-center gap-2 mb-3">
                  <CheckCircle className="w-5 h-5 text-green-500" />
                  <h3 className="text-lg font-bold text-gray-900">A Te Style DNA-d</h3>
                </div>
                <p className="text-gray-700 leading-relaxed whitespace-pre-wrap">{styleDNA}</p>
              </div>

              {/* Recommendations */}
              {recommendations.length > 0 && (
                <div>
                  <h4 className="text-sm font-bold text-gray-700 mb-3 flex items-center gap-2">
                    <Star className="w-4 h-4 text-amber-500" />
                    Szemlyre szabott ajnlatok ({recommendations.length})
                  </h4>
                  <div className="grid grid-cols-3 gap-2">
                    {recommendations.slice(0, 6).map((product) => (
                      <div key={product.id} className="bg-gray-50 rounded-lg p-2 text-center hover:bg-primary-50 transition-colors cursor-pointer">
                        <img
                          src={product.images?.[0] || product.image || '/placeholder.png'}
                          alt={product.name}
                          className="w-full aspect-square object-contain rounded bg-white mb-1"
                          onError={(e) => { e.target.src = 'data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="100" height="100"><rect fill="%23f3f4f6" width="100" height="100"/><text x="50%" y="50%" text-anchor="middle" dy=".3em" fill="%239ca3af" font-size="12">Kp</text></svg>'; }}
                        />
                        <p className="text-[10px] text-gray-600 truncate">{product.name}</p>
                        <p className="text-xs font-bold text-primary-500">{formatPrice(product.salePrice || product.price)}</p>
                      </div>
                    ))}
                  </div>
                  {recommendations.length > 6 && (
                    <p className="text-center text-xs text-gray-500 mt-2">
                      + {recommendations.length - 6} tovbbi ajnlat
                    </p>
                  )}
                </div>
              )}

              <button
                onClick={onClose}
                className="w-full bg-gradient-to-r from-primary-500 to-secondary-700 text-white py-3.5 rounded-xl font-bold hover:opacity-90 transition-opacity flex items-center justify-center gap-2"
              >
                <ShoppingBag className="w-5 h-5" />
                Termkek megtekintse
              </button>
            </div>
          ) : (
            <div>
              {/* Question */}
              <div className="flex items-center gap-3 mb-5">
                <div className="w-10 h-10 bg-primary-100 rounded-xl flex items-center justify-center shrink-0">
                  <QuestionIcon className="w-5 h-5 text-primary-500" />
                </div>
                <h3 className="text-lg font-bold text-gray-900">{currentQuestion.question}</h3>
              </div>

              {/* Options */}
              <div className="space-y-2">
                {currentQuestion.options.map((option) => {
                  const OptionIcon = option.icon;
                  const isSelected = answers[currentQuestion.id] === option.id;
                  
                  return (
                    <button
                      key={option.id}
                      onClick={() => handleAnswer(option.id)}
                      className={`
                        w-full p-3.5 rounded-xl border-2 text-left transition-all
                        flex items-center gap-3
                        ${isSelected 
                          ? 'border-primary-500 bg-primary-50' 
                          : 'border-gray-200 hover:border-primary-300 hover:bg-gray-50'
                        }
                      `}
                    >
                      <div className={`w-10 h-10 rounded-xl flex items-center justify-center shrink-0 ${option.color}`}>
                        <OptionIcon className="w-5 h-5" />
                      </div>
                      <span className="text-sm font-medium text-gray-800 flex-1">{option.label}</span>
                      {isSelected && <ArrowRight className="w-5 h-5 text-primary-500" />}
                    </button>
                  );
                })}
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default AIStyleQuiz;
